import React, { useRef, useState, useEffect } from "react"
import LeftMenu from "./LeftMenu"
import { Chart } from 'react-charts'
import { Form, Button, Card, Alert, CardGroup, CardDeck, Table, Accordion, ListGroup, DropdownButton, Dropdown } from "react-bootstrap"
import Select from 'react-select'
import "./Assets.css"

export default function Assets() {

    const [click, setClick] = useState(false);
  
    const handleClick = () => setClick(!click);

    const data = React.useMemo(
        () => [
          {
            label: 'Series 1',
            data: [[12, 2], [14, 2], [16, 4], [18, 2], [20, 7]]
          }
        ],
        []
      )
     
    const axes = React.useMemo(
        () => [
          { primary: true, type: 'time', position: 'bottom' },
          { type: 'linear', position: 'left' }
        ],
        []
    )
    
    const assets = [
        { value: 'lands', label: 'Lands' },
        { value: 'houses', label: 'Houses' }
    ]

    const planets = [
        { value: 'mars', label: 'Mars' },
        { value: 'uranus', label: 'Uranus' },
        { value: 'moon', label: 'Moon' },
        { value: 'saturn', label: 'Saturn' },
        { value: 'jupiter', label: 'Jupiter' },
        { value: 'neptun', label: 'Neptun' },
        { value: 'pluto', label: 'Pluto' }
    ]

    const stocks = [
        { value: 'transport', label: 'Transport' },
        { value: 'medicine', label: 'Medicine' },
        { value: 'education', label: 'Education' },
        { value: 'infrastructure', label: 'Infrastructure' },
        { value: 'tesla', label: 'Tesla' },
        { value: 'spaceX', label: 'SpaceX' },
        { value: 'agriculture', label: 'Agriculture' }
    ]

    const options = [
        { value: 'assets', label: 'Assets' },
        { value: 'stocks', label: 'Stocks' }
    ]

    const [list, setList] = useState([
        { value: '', label: '' }
    ]);

    const [extraOption, setExtraOption] = useState('');

    const handleChange = (e) => {
        if (e.value == "stocks") {
            setList(stocks);
            setExtraOption('');
        }
        if (e.value == "assets") {
            setList(assets);
            setExtraOption('set');
        }
    };

    let menu;
    var w = window.innerWidth

    if(click) {
        if(w<=767) {
            menu=<LeftMenu type="lm-new-min"/>
        }
        else
            menu=<LeftMenu type="lm-new"/>
    }
    else {
        if(w<=767) {
            menu=<LeftMenu type="lm-min"/>
        }
        else
            menu=<LeftMenu type="lm"/>
    }
  
    return (
        
        <>
            {menu}
            <div className={click ? 'a-new' : 'a-body'}>
                <header class="a-header">
                    <div className='left-menu-icon' onClick={handleClick}>
                        <i className={click ? 'fa fa-bars' : 'fa fa-bars'} id='btn'/>
                    </div>
                    <h3 className="username"><span className="user-icon"></span>username</h3>
                </header>
                <div class="a-content">
                    <CardDeck>
                        <Card>
                            <Card.Body>
                                <Card.Title>Assets</Card.Title>
                                <Table responsive>
                                    <thead>
                                        <tr>
                                        <th>#</th>
                                        <th>Planet</th>
                                        <th>Lands</th>
                                        <th>Houses</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                        <td><span className="planet1"></span></td>
                                        <td>Mars</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet2"></span></td>
                                        <td>Uranus</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet3"></span></td>
                                        <td>Moon</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet4"></span></td>
                                        <td>Saturn</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet5"></span></td>
                                        <td>Jupiter</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet6"></span></td>
                                        <td>Neptun</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="planet7"></span></td>
                                        <td>Pluto</td>
                                        <td>2</td>
                                        <td>2</td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                        <Card>
                            <Card.Body>
                                <Card.Title>Stocks</Card.Title>
                                <Table responsive>
                                    <thead>
                                        <tr>
                                        <th>#</th>
                                        <th>Domain</th>
                                        <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                        <td><span className="stock1"></span></td>
                                        <td>Transport</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock2"></span></td>
                                        <td>Medicine</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock3"></span></td>
                                        <td>Education</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock4"></span></td>
                                        <td>Infrastructure</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock5"></span></td>
                                        <td>Tesla</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock6"></span></td>
                                        <td>SpaceX</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="stock7"></span></td>
                                        <td>Agriculture</td>
                                        <td>2</td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </CardDeck>
                    <br></br>
                    <CardDeck>
                        <Card className="sellCard">
                            <Card.Body>
                            <h2 className="text-center mb-4">Sell assets</h2>
                            <Form>
                                <Form.Group id="cardNumber">
                                <Form.Label>Items</Form.Label>
                                <Select options={options} onChange={handleChange} >
                                </Select>
                                {extraOption == 'set' && (
                                    <br></br>
                                )}
                                {extraOption == 'set' && (
                                    <Select options={planets}/>
                                )}
                                <br></br>
                                <Select options={list}>
                                </Select>
                                </Form.Group>
                                <Form.Group id="amount">
                                <Form.Label>Amount</Form.Label>
                                <Form.Control
                                    type="number"
                                />
                                </Form.Group>
                                <Button type="submit">
                                Sell
                                </Button>
                            </Form>
                            </Card.Body>
                        </Card>
                        <Card>
                            <br></br>
                            <Card>
                                <Card.Body>
                                    <Card.Title>Coins</Card.Title>
                                    <Table responsive>
                                    <tbody>
                                        <tr>
                                        <td><span className="firstCoin"></span></td>
                                        <td>Coin1</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="secondCoin"></span></td>
                                        <td>Coin2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="thirdCoin"></span></td>
                                        <td>Coin3</td>
                                        <td>2</td>
                                        </tr>
                                    </tbody>
                                    </Table>
                                </Card.Body>
                            </Card>
                            <br></br>
                        </Card>
                    </CardDeck>
                </div>
            </div>
        </>
    )
}
