import React, { useRef, useState } from "react"
import LeftMenu from "../userspace/LeftMenu"
import { Card, Table, CardDeck } from "react-bootstrap"
import '../../../node_modules/react-vis/dist/style.css';
import {XYPlot, LineSeries, XAxis, YAxis, VerticalGridLines, HorizontalGridLines, VerticalBarSeries, VerticalBarSeriesCanvas, HorizontalBarSeries, DiscreteColorLegend, makeWidthFlexible, LabelSeries, MarkSeries} from 'react-vis';
import { Chart } from 'react-charts'
import "./Admin.css"

export default function Admin() {

    const FlexibleXYPlot = makeWidthFlexible(XYPlot); 

    const [click, setClick] = useState(false);
  
    const handleClick = () => setClick(!click);

    const [useCanvas,setUseCanvas] = useState(false);

    const BarSeries = useCanvas ? VerticalBarSeriesCanvas : VerticalBarSeries;

    const data = [
        {x: 0, y: 8},
        {x: 1, y: 5},
        {x: 2, y: 4},
        {x: 3, y: 9},
        {x: 4, y: 1},
        {x: 5, y: 7},
        {x: 6, y: 6},
        {x: 7, y: 3},
        {x: 8, y: 2},
        {x: 9, y: 0}
    ];

    const data2 = [
        {
          x: 3,
          y: 7,
          label: 'Wigglytuff',
          size: 30,
          style: {fontSize: 20},
          rotation: 45
        },
        {x: 2, y: 4, label: 'Psyduck', size: 10},
        {x: 1, y: 20, label: 'Geodude', size: 1},
        {x: 4, y: 12, label: 'Ditto', size: 12, rotation: 45},
        {x: 1, y: 14, label: 'Snorlax', size: 4}
    ];

    let menu;
    var w = window.innerWidth

    if(click) {
        if(w<=767) {
            menu=<LeftMenu type="lm-new-min"/>
        }
        else
            menu=<LeftMenu type="lm-new"/>
    }
    else {
        if(w<=767) {
            menu=<LeftMenu type="lm-min"/>
        }
        else
            menu=<LeftMenu type="lm"/>
    }
  
    return (
        
        <>
          {menu}
          <div className={click ? 'u-new' : 'u-body'}>
                <header class="u-header">
                    <div className='left-menu-icon' onClick={handleClick}>
                    <i className={click ? 'fa fa-bars' : 'fa fa-bars'} id='btn'/>
                    </div>
                    <h3 className="username"><span className="user-icon"></span>username</h3>
                </header>
                <div class="u-table">
                    <h3 className="d-h1">Remaining items</h3>
                    <CardDeck>
                        <Card>
                            <span className="remainings1"></span>
                            <Card.Body>
                            <Card.Title>Lands</Card.Title>
                            </Card.Body>
                            <Card.Footer>
                                <h4>10000</h4>
                            </Card.Footer>
                        </Card>
                        <Card>
                            <span className="remainings2"></span>
                            <Card.Body>
                            <Card.Title>Houses</Card.Title>
                            </Card.Body>
                            <Card.Footer>
                                <h4>100000</h4>
                            </Card.Footer>
                        </Card>
                        <Card>
                            <span className="remainings3"></span>
                            <Card.Body>
                            <Card.Title>Stocks</Card.Title>
                            </Card.Body>
                            <Card.Footer>
                                <h4>10000</h4>
                            </Card.Footer>
                        </Card>
                        <Card>
                            <span className="remainings4"></span>
                            <Card.Body>
                            <Card.Title>Coins</Card.Title>
                            </Card.Body>
                            <Card.Footer>
                                <h4>100000</h4>
                            </Card.Footer>
                        </Card>
                    </CardDeck>
                    <br></br>
                    <Card responsive>
                        <Card.Title className="admin-title-chart">Chart</Card.Title>
                        <FlexibleXYPlot height={300}>
                                <VerticalGridLines />
                                <HorizontalGridLines />
                                <XAxis />
                                <YAxis />
                                <LineSeries data={data} />
                        </FlexibleXYPlot>
                    </Card>
                    <br></br>
                    <CardDeck responsive>
                        <Card responsive>
                            <Card.Title className="admin-title-chart">Chart</Card.Title>
                            <FlexibleXYPlot height={300}>
                                <VerticalGridLines />
                                <HorizontalGridLines />
                                <XAxis />
                                <YAxis />
                                <LineSeries data={data} />
                            </FlexibleXYPlot>
                        </Card>
                        <Card>
                            <Card.Title className="admin-title-chart">Chart</Card.Title>
                            <FlexibleXYPlot
                            className="clustered-stacked-bar-chart-example"
                            xType="ordinal"
                            stackBy="y"
                            height={300}
                            >
                            <DiscreteColorLegend
                                style={{position: 'absolute', left: '50px', top: '10px'}}
                                orientation="horizontal"
                                items={[
                                {
                                    title: 'Apples',
                                    color: '#12939A'
                                },
                                {
                                    title: 'Oranges',
                                    color: '#79C7E3'
                                }
                                ]}
                            />
                            <VerticalGridLines />
                            <HorizontalGridLines />
                            <XAxis />
                            <YAxis />
                            <BarSeries
                                cluster="2015"
                                color="#12939A"
                                data={[
                                {x: 'Q1', y: 10},
                                {x: 'Q2', y: 5},
                                {x: 'Q3', y: 15},
                                {x: 'Q4', y: 20}
                                ]}
                            />
                            <BarSeries
                                cluster="2015"
                                color="#79C7E3"
                                data={[
                                {x: 'Q1', y: 3},
                                {x: 'Q2', y: 7},
                                {x: 'Q3', y: 2},
                                {x: 'Q4', y: 1}
                                ]}
                            />
                            <BarSeries
                                cluster="2016"
                                color="#12939A"
                                data={[
                                {x: 'Q1', y: 3},
                                {x: 'Q2', y: 8},
                                {x: 'Q3', y: 11},
                                {x: 'Q4', y: 19}
                                ]}
                            />
                            <BarSeries
                                cluster="2016"
                                color="#79C7E3"
                                data={[
                                {x: 'Q1', y: 22},
                                {x: 'Q2', y: 2},
                                {x: 'Q3', y: 22},
                                {x: 'Q4', y: 18}
                                ]}
                            />
                            </FlexibleXYPlot>
                        </Card>
                    </CardDeck>
                    <br></br>
                    <CardDeck>    
                        <Card>
                            <Card.Title className="admin-title-chart">Chart</Card.Title>
                            <FlexibleXYPlot height={300}>
                                <VerticalGridLines />
                                <HorizontalGridLines />
                                <XAxis />
                                <YAxis />
                                <LineSeries data={data} />
                            </FlexibleXYPlot>
                        </Card>
                        <Card>
                            <Card.Title className="admin-title-chart">Chart</Card.Title>
                            <FlexibleXYPlot yDomain={[-1, 22]} xDomain={[-1, 5]} height={300}>
                                <XAxis />
                                <YAxis />
                                <MarkSeries
                                    className="mark-series-example"
                                    strokeWidth={2}
                                    sizeRange={[5, 15]}
                                    data={data2}
                                />
                                <LabelSeries animation allowOffsetToBeReversed data={data2} />
                            </FlexibleXYPlot>
                        </Card>
                    </CardDeck>
                    <br></br>
                    <Card>
                        <span class="usersAstronaut"></span>
                        <br></br>
                        <Card.Body>
                            <Card.Title className="title-users"><h3>Users</h3></Card.Title>
                            <Table bordered hover responsive>
                            <thead>
                                <tr>
                                <th>#</th>
                                <th>Username</th>
                                <th>Lands</th>
                                <th>Alien coins</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                <td>1</td>
                                <td>user@gmail.com</td>
                                <td>2</td>
                                <td>100</td>
                                </tr>
                                <tr>
                                <td>2</td>
                                <td>user@gmail.com</td>
                                <td>2</td>
                                <td>100</td>
                                </tr>
                                <tr>
                                <td>3</td>
                                <td>user@gmail.com</td>
                                <td>2</td>
                                <td>100</td>
                                </tr>
                            </tbody>
                            </Table>
                        </Card.Body>
                    </Card>
                </div>
            </div> 
        </>
    )
}
