import React, { useRef, useState } from 'react'
import LeftMenu from "./LeftMenu"
import { Provider, useSelector, useDispatch } from 'react-redux'
import { createStore } from 'redux'
import { Form, Button, Card, Alert, CardGroup, CardDeck, Table } from "react-bootstrap"
import {XYPlot, LineSeries, XAxis, YAxis, VerticalGridLines, HorizontalGridLines, VerticalBarSeries, VerticalBarSeriesCanvas, HorizontalBarSeries, DiscreteColorLegend, makeWidthFlexible, LabelSeries, MarkSeries} from 'react-vis';
import { Chart } from 'react-charts'
import "./Wallet.css"
import "./LeftMenu.css"

export default function Wallet() {
    const FlexibleXYPlot = makeWidthFlexible(XYPlot); 
    const [click, setClick] = useState(false);
  
    const handleClick = () => setClick(!click);


    const data = React.useMemo(
        () => [
          {
            label: 'Series 1',
            data: [{ x: 1, y: 15 }, { x: 2, y: 15 }, { x: 3, y: 15 }]
          },
          {
            label: 'Series 2',
            data: [{ x: 1, y: 10 }, { x: 2, y: 10 }, { x: 3, y: 10 }]
          },
          {
            label: 'Series 3',
            data: [{ x: 1, y: 5 }, { x: 2, y: 5 }, { x: 3, y: 5 }]
          }
        ],
        []
    )
     
    const axes = React.useMemo(
        () => [
          { primary: true, type: 'linear', position: 'bottom' },
          { type: 'linear', position: 'left' }
        ],
        []
    )

    const data2 = [
        {
          x: 3,
          y: 7,
          label: 'Wigglytuff',
          size: 30,
          style: {fontSize: 20},
          rotation: 45
        },
        {x: 2, y: 4, label: 'Psyduck', size: 10},
        {x: 1, y: 20, label: 'Geodude', size: 1},
        {x: 4, y: 12, label: 'Ditto', size: 12, rotation: 45},
        {x: 1, y: 14, label: 'Snorlax', size: 4}
    ];

    let menu;
    var w = window.innerWidth

    if(click) {
        if(w<=767) {
            menu=<LeftMenu type="lm-new-min"/>
        }
        else
            menu=<LeftMenu type="lm-new"/>
    }
    else {
        if(w<=767) {
            menu=<LeftMenu type="lm-min"/>
        }
        else
            menu=<LeftMenu type="lm"/>
    }

    return (
        
        <>
            {menu}
            <div className={click ? 'w-new' : 'w-body'}>
                <header class="w-header">
                    <div className='left-menu-icon' onClick={handleClick}>
                        <i className={click ? 'fa fa-bars' : 'fa fa-bars'} id='btn'/>
                    </div>
                    <h3 className="username"><span className="user-icon"></span>username</h3>
                </header>
                <div class="w-content">
                    <Card>
                        <Card.Title className="admin-title-chart">Chart</Card.Title>
                        <FlexibleXYPlot yDomain={[-1, 22]} xDomain={[-1, 5]} height={300}>
                            <XAxis />
                             <YAxis />
                            <MarkSeries
                                className="mark-series-example"
                                strokeWidth={2}
                                sizeRange={[5, 15]}
                                data={data2}
                            />
                            <LabelSeries animation allowOffsetToBeReversed data={data2} />
                        </FlexibleXYPlot>
                    </Card>
                    <CardDeck className="w-section">
                        <Card>
                            <br></br>
                            <Card>
                                <Card.Body>
                                    <Card.Title>Holdings</Card.Title>
                                    <Table responsive>
                                    <tbody>
                                        <tr>
                                        <td><span className="firstCoin"></span></td>
                                        <td>Coin1</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="secondCoin"></span></td>
                                        <td>Coin2</td>
                                        <td>2</td>
                                        </tr>
                                        <tr>
                                        <td><span class="thirdCoin"></span></td>
                                        <td>Coin3</td>
                                        <td>2</td>
                                        </tr>
                                    </tbody>
                                    </Table>
                                </Card.Body>
                            </Card>
                            <br></br>
                            <Card>
                                <Card.Body>
                                    <Card.Title>Currency</Card.Title>
                                    <Table responsive >
                                    <thead>
                                        <tr>
                                        <th>#</th>
                                        <th>Coins</th>
                                        <th>Euro</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                        <td><span className="firstCoin"></span></td>
                                        <td><Form.Control type="number"/></td>
                                        <td><Form.Control type="number"/></td>
                                        </tr>
                                        <tr>
                                        <td><span class="secondCoin"></span></td>
                                        <td><Form.Control type="number"/></td>
                                        <td><Form.Control type="number"/></td>
                                        </tr>
                                        <tr>
                                        <td><span class="thirdCoin"></span></td>
                                        <td><Form.Control type="number"/></td>
                                        <td><Form.Control type="number"/></td>
                                        </tr>
                                    </tbody>
                                    </Table>
                                </Card.Body>
                            </Card>
                            <br></br>
                        </Card>
                        <Card className="buyCard">
                            <Card.Body>
                            <h2 className="text-center mb-4">Buy alien coins</h2>
                            <Form>
                                <Form.Group id="cardNumber">
                                <Form.Label>Card Number</Form.Label>
                                <Form.Control
                                    type="tel" pattern="[0-9\s]{13,19}" placeholder="xxxx xxxx xxxx xxxx"
                                />
                                </Form.Group>
                                <Form.Group id="cvv">
                                <Form.Label>CVV</Form.Label>
                                <Form.Control
                                    type="tel"
                                />
                                </Form.Group>
                                <Form.Group id="expirationDate">
                                <Form.Label>Expiration date</Form.Label>
                                <Form.Control
                                    type="date"
                                />
                                </Form.Group>
                                <Form.Group id="cardName">
                                <Form.Label>Owner name</Form.Label>
                                <Form.Control
                                    type="text"
                                />
                                </Form.Group>
                                <Form.Group id="amount">
                                <Form.Label>Amount</Form.Label>
                                <Form.Control
                                    type="number"
                                />
                                </Form.Group>
                                <Button type="submit">
                                Buy
                                </Button>
                            </Form>
                            </Card.Body>
                        </Card>
                    </CardDeck>
                </div>
            </div>
        </>
    )
}
