import React, { useRef, useState } from "react"
import { Form, Button, Card, Alert } from "react-bootstrap"
import { useAuth } from "../../contexts/AuthContext"
import { Link, useHistory } from "react-router-dom"
import LeftMenu from "./LeftMenu"
import "./Profile.css"

export default function Profile() {
  const emailRef = useRef()
  const passwordRef = useRef()
  const passwordConfirmRef = useRef()
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)
  const history = useHistory()

  const [click, setClick] = useState(false);
  
  const handleClick = () => setClick(!click);

  function handleSubmit(e) {
    e.preventDefault()
    if (passwordRef.current.value !== passwordConfirmRef.current.value) {
      return setError("Passwords do not match")
    }

    const promises = []
    setLoading(true)
    setError("")

    // if (emailRef.current.value !== currentUser.email) {
    //   promises.push(updateEmail(emailRef.current.value))
    // }
    // if (passwordRef.current.value) {
    //   promises.push(updatePassword(passwordRef.current.value))
    // }

    Promise.all(promises)
      .then(() => {
        history.push("/")
      })
      .catch(() => {
        setError("Failed to update account")
      })
      .finally(() => {
        setLoading(false)
      })
  }

  let menu;
  var w = window.innerWidth

  if(click) {
      if(w<=767) {
          menu=<LeftMenu type="lm-new-min"/>
      }
      else
          menu=<LeftMenu type="lm-new"/>
  }
  else {
      if(w<=767) {
          menu=<LeftMenu type="lm-min"/>
      }
      else
          menu=<LeftMenu type="lm"/>
  }

  return (
      
      <>
        {menu}
        <div className={click ? 'p-new' : 'p-body'}>
            <header class="p-header">
              <div className='left-menu-icon' onClick={handleClick}>
                <i className={click ? 'fa fa-bars' : 'fa fa-bars'} id='btn'/>
              </div>
              <h3 className="username"><span className="user-icon"></span>username</h3>
            </header>
            <div class="p-profile">
                <Card>
                    <Card.Body>
                    <span class="userAstronaut"></span>
                    <h2 className="text-center mb-4">Personal info</h2>
                    {error && <Alert variant="danger">{error}</Alert>}
                    <br></br>
                    <Form onSubmit={handleSubmit}>
                        <Form.Group id="email">
                        <Form.Label>Email</Form.Label>
                        <Form.Control
                            type="email"
                        />
                        </Form.Group>
                        <Form.Group id="firstname">
                        <Form.Label>Firstname</Form.Label>
                        <Form.Control
                            type="text"
                        />
                        </Form.Group>
                        <Form.Group id="lastname">
                        <Form.Label>Lastname</Form.Label>
                        <Form.Control
                            type="text"
                        />
                        </Form.Group>
                        <Form.Group id="phonenumber">
                        <Form.Label>Phone number</Form.Label>
                        <Form.Control
                            type="text"
                        />
                        </Form.Group>
                        <Form.Group id="address">
                        <Form.Label>Address</Form.Label>
                        <Form.Control
                            type="text"
                        />
                        </Form.Group>
                        <Form.Group id="password">
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            type="password"
                            ref={passwordRef}
                            placeholder="Leave blank to keep the same"
                        />
                        </Form.Group>
                        <Form.Group id="password-confirm">
                        <Form.Label>Password Confirmation</Form.Label>
                        <Form.Control
                            type="password"
                            ref={passwordConfirmRef}
                            placeholder="Leave blank to keep the same"
                        />
                        </Form.Group>
                        <Button disabled={loading} type="submit">
                        Update
                        </Button>
                    </Form>
                    </Card.Body>
                </Card>
            </div>
        </div>
    </>
  )
}
